import { useTranslation } from "react-i18next";
import LayoutDefault from "../layouts/LayoutDefault";
import useInvoicesStore from "../stores/useInvoicesStore";
import useFetchDataStore from "../stores/useFetchDataStore";
import { useCallback, useEffect, useState } from "react";
import { API } from "../stores/API/Endpoints";
import { TypeFormProduct, setTypeFormProduct } from "../types/TypesForm";
import useAuthStore from "../stores/useAuthStore";
import Modal from "../components/controls/Modal";
import ProductForm from "../components/forms/ProductForm";
import SearchInput from "../components/controls/SeachInput";
import { Link } from "react-router-dom";
import {
  IconChevronRight,
  IconNew,
  IconRefresh,
  IconSearch,
} from "../icons/Icon";

const InvoicesPage = () => {
  const { t } = useTranslation();
  const [formValues, setFormValue] = useState(TypeFormProduct);
  const [searchValue, setSearchValue] = useState("");
  const [pageInvoices, setPageInvoices] = useState(1);
  const [sizePages, setSizePages] = useState(25);
  const [isModalOpen, setModalOpen] = useState(false);

  const { auth } = useAuthStore((state) => ({
    auth: state.auth,
  }));

  const { setProduct } = useInvoicesStore((state) => ({
    setProduct: state.setProduct,
  }));

  const { callApi, data, getData, refreshData, loading, error } =
    useFetchDataStore((state) => ({
      callApi: state.callApi,
      data: state.data,
      getData: state.getData,
      refreshData: state.refreshData,
      loading: state.loading,
      error: state.error,
    }));

  const products = getData(
    `${API.product.products.url}/?page_size=${sizePages}&page_number=${pageInvoices}`
  );

  useEffect(() => {
    if (user?.userData?.id_usuario) {
      setFormValue((prev) => ({
        ...prev,
        uid_creacion: user.userData.id_usuario,
      }));
    }
  }, [user]);

  const handleRefreshInvoices = useCallback(() => {
    refreshData(
      `${API.product.products.url}/list?page_size=${sizePages}&page_number=${pageInvoices}`
    );
  }, [refreshData, pageInvoices, sizePages]);

  useEffect(() => {
    handleRefreshInvoices();
  }, [pageInvoices, handleRefreshInvoices]);

  const handleChange = (event) => {
    const { name, value, type, checked } = event.target;
    const inputValue = type === "checkbox" ? (checked ? 1 : 0) : value;
    setFormValue((prev) => ({
      ...prev,
      [name]: inputValue,
    }));
  };

  const handleSubmit = (event) => {
    event.preventDefault();
    callApi(API.product.save.url, API.product.save.method, formValues);
  };

  const openModal = () => setModalOpen(true);
  const closeModal = () => {
    setFormValue(TypeFormProduct);
    setModalOpen(false);
  };

  const handleUpdate = (event) => {
    const id = event.currentTarget.value;
    const edit = products.content.find(
      (item) => item.id_producto === Number(id)
    );
    const newValue = setTypeFormProduct(edit);
    setFormValue(newValue);
    openModal();
  };

  const handleProductSearch = (searchText) => {
    callApi(
      API.product.search.url,
      API.product.search.method,
      searchText.trim()
    );
    const data = getData();
    if (data == []) {
      handleRefreshInvoices();
    }
  };
  return (
    <LayoutDefault>
      <div className="flex-1 w-full">
        <section className="bg-white px-4 pt-4 inline-flex justify-between items-center w-full border-b mb-4 ">
          <h1 className="text-2xl font-semibold mb-1 pr-4 inline-flex items-center gap-2">
            <Link to="/"> {t("home.title")}</Link>
            <IconChevronRight />
            <span>{t("invoices.title")}</span>
          </h1>
          <span>
            <button
              className="bg-orange-400 text-white p-1 shadow hover:scale-95"
              value={"refresh"}
              onClick={handleRefreshInvoices}
            >
              {/* Icon for refresh */}
              <IconRefresh />
            </button>
          </span>
        </section>
        <section className="w-full bg-white divide-y border-b pb-4 mb-2.5">
          <section className="w-full px-5 mx-auto flex justify-between items-center">
            <div className="w-44">
              <button
                className={`inline-flex gap-4 max-w-32 rounded-sm w-full min-w-20 outline-green-400 px-2.5 py-1.5 text-white font-semibold relative overflow-hidden capitalize group ${
                  !loading
                    ? "bg-green-600 active:scale-95 shadow"
                    : "bg-green-600/50"
                }`}
                onClick={openModal}
                type="submit"
                disabled={loading}
              >
                <IconNew />
                {t("categories.new")}
                <div
                  className={`absolute top-0 -inset-full h-full w-1/2 z-5 block transform -skew-x-12 bg-gradient-to-r from-transparent to-white opacity-40 ${
                    loading
                      ? "animate-shineInfinity"
                      : "group-hover:animate-shine"
                  } `}
                />
              </button>
            </div>
            <div className="inline-flex">
              <SearchInput
                searchValue={searchValue}
                setSearchValue={setSearchValue}
              />
              <button
                className={`max-w-24 outline-sky-400 px-2.5 py-1.5 text-white font-semibold relative overflow-hidden group ${
                  !loading
                    ? "bg-sky-600 active:scale-95 shadow"
                    : "bg-sky-600/50"
                }`}
                onClick={handleProductSearch}
                type="button"
                disabled={loading}
              >
                <IconSearch />
                <div
                  className={`absolute top-0 -inset-full h-full w-1/2 z-5 block transform -skew-x-12 bg-gradient-to-r from-transparent to-white opacity-40 ${
                    loading
                      ? "animate-shineInfinity"
                      : "group-hover:animate-shine"
                  } `}
                />
              </button>
            </div>
          </section>
        </section>
        {/* Modal and forms */}
        <Modal isOpen={isModalOpen} onClose={closeModal}>
          <ProductForm
            loading={loading}
            formValues={formValues}
            setFormValue={setFormValue}
            handleChange={handleChange}
            handleSubmit={handleSubmit}
          />
        </Modal>
      </div>
    </LayoutDefault>
  );
};

export default InvoicesPage;
